#myproject {
    background-color: #f0eeeb;
}

@mixin btn-style {
    border: none;
    cursor: pointer;
    background: transparent;
}

.my-projects-container {
    margin: 0 auto;
    padding: 50px 20px;
}

.section-title {
    @include font-gray50();
    text-align: left;
    align-items: center;
    justify-content: center;
    margin-bottom: 3.5rem;
}

.project-list {
    border-top: 1px solid #505050;
}

.project-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 0;

    .project-info {
        h3 {
            @include font-gray24();
            margin: 0 0 0.5rem 0;
        }
        p {
            font-size: 16px;
            color: #505050;
            margin: 0 0 1rem 0;
        }
        .tech-stack {
            font-size: 16px;
            color: #505050;
            span {
                font-size: 16px;
                color: #505050;
            }
        }
    }

    .project-link {
        color: #505050;
        font-weight: bold;
        text-decoration: underline;
        cursor: pointer;
    }
}

.bottom-navigation {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 80px;
    gap: 16px;

    .nav-link {
        background-color: #050505;
        color: #f97316;
        width: 320px;
        max-width: 90vw;
        min-width: 120px;
        height: 48px;
        font-size: 18px;
        cursor: pointer;
        border-radius: 16px;
        border: none;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: background 0.2s;
        margin: 0 auto;
        padding: 0;
    }
}

.arrow-container-1 {
    width: 100vw;
    margin: 32px 0 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: relative;
    transform: translateX(-50%);
    left: 50%;
    .bottom-arrow-1 {
        @include btn-style();
        background: none;
        color: #f97316;
        font-size: 2.5rem;
    }
}

// 반응형 처리
@include desktop {
    .my-projects-container {
        padding: 30px 10px;
    }
    .section-title {
        font-size: 40px;
        margin-bottom: 2rem;
    }
    .project-item {
        padding: 1rem 0;
        .project-info {
            h3 {
                font-size: 20px;
            }
            p,
            .tech-stack,
            .tech-stack span {
                font-size: 13px;
            }
        }
        .project-link {
            font-size: 13px;
        }
    }
    .bottom-navigation {
        margin-top: 40px;
        .nav-link {
            width: 240px;
            height: 40px;
            font-size: 15px;
            border-radius: 13px;
        }
    }
    .arrow-container-1 .bottom-arrow-1 {
        font-size: 2rem;
         left: 50%;
    }
}

@include tablet {
    .my-projects-container {
        padding: 16px 4px;
    }
    .section-title {
        font-size: 28px;
        margin-bottom: 1.2rem;
    }
    .project-item {
        flex-direction: column;
        align-items: flex-start;
        gap: 8px;
        .project-info {
            h3 {
                font-size: 16px;
            }
            p,
            .tech-stack,
            .tech-stack span {
                font-size: 11px;
            }
        }
        .project-link {
            font-size: 11px;
        }
    }
    .bottom-navigation {
        margin-top: 24px;
        gap: 12px;
        .nav-link {
            width: 180px;
            height: 36px;
            font-size: 13px;
            border-radius: 10px;
        }
    }
    .arrow-container-1 {
        margin-top: 18px;
        .bottom-arrow-1 {
            font-size: 1.5rem;
        }
    }
}

@include mobile {
    .my-projects-container {
        padding: 8px 2px;
    }
    .section-title {
        font-size: 16px;
        margin-bottom: 0.7rem;
    }
    .project-item {
        flex-direction: column;
        align-items: flex-start;
        gap: 4px;
        padding: 0.5rem 0;
        .project-info {
            h3 {
                font-size: 12px;
            }
            p,
            .tech-stack,
            .tech-stack span {
                font-size: 9px;
            }
        }
        .project-link {
            font-size: 9px;
        }
    }
    .bottom-navigation {
        margin-top: 12px;
        gap: 8px;
        padding: 0 8px;
        .nav-link {
            width: 100%;
            max-width: 260px;
            min-width: 100px;
            height: 32px;
            font-size: 11px;
            border-radius: 8px;
            margin: 0 auto;
            padding: 0;
        }
    }
    .arrow-container-1 {
        margin-top: 12px;
        
        .bottom-arrow-1 {
       left: 50%;
            font-size: 2rem;
        }
    }
}