#aboutme {
  background-color: #f0eeeb;
  padding: 4rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;

  h2 {
    font-size: 100px;
    font-weight: 600;
    margin-bottom: 60px;
    color: #505050;
  }

  .content {
    display: flex;
    align-items: flex-start; 
    gap: 40px; 

    img {
      width: 270px;
      height: 350px; 
      object-fit: cover;
      border: 1px solid #ccc;
      background-color: #fff;
      margin-bottom: 0;
    }

    .info {
      list-style: none;
      padding: 0;
      margin: 0;

      li {
        display: flex;
        align-items: center;
        font-size: 18px;
        color: #222;
        margin-bottom: 24px;

        svg {
          font-size: 35px;
          margin-right: 12px;
          color: #222;
        }

        span {
          @include font-gray24();
        }
      }
    }
  }

  .description {
    margin-top: 50px;
    @include font-gray24();
    margin-bottom: 58px;
    text-align: center;
  }

  .git-container {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #050505;
    border-radius: 20px;
    padding: 12px 30px;
    cursor: pointer;
    user-select: none;

    a {
      display: flex; 
      align-items: center;
      text-decoration: none; 
      color: inherit; 
    }

    p {
      margin-left: 12px;
      font-size: 24px;
      font-weight: 600;
      color: #f97316;
    }

    .git {
      font-size: 28px;
      color: #f97316;
      background: none;
    }
  }

  // 반응형 처리
  @include desktop {
    padding: 3rem 1.5rem;
    h2 {
      font-size: 65px;
      margin-bottom: 40px;
    }
    .content {
      gap: 24px;
      img {
        width: 180px;
        height: 240px;
      }
      .info li {
        font-size: 16px;
        svg {
          font-size: 28px;
        }
      }
    }
    .description {
      font-size: 18px;
      margin-top: 30px;
      margin-bottom: 40px;
    }
    .git-container {
      padding: 10px 20px;
      p {
        font-size: 20px;
      }
      .git {
        font-size: 22px;
      }
    }
  }

  @include tablet {
    padding: 2rem 1rem;
    h2 {
      font-size: 40px;
      margin-bottom: 24px;
    }
    .content {
      flex-direction: column;
      align-items: center;
      gap: 16px;
      img {
        width: 120px;
        height: 160px;
      }
      .info li {
        font-size: 14px;
        svg {
          font-size: 20px;
        }
      }
    }
    .description {
      font-size: 15px;
      margin-top: 18px;
      margin-bottom: 24px;
    }
    .git-container {
      padding: 8px 12px;
      p {
        font-size: 16px;
      }
      .git {
        font-size: 16px;
      }
    }
  }

  @include mobile {
    padding: 1rem 0.5rem;
    h2 {
      font-size: 22px;
      margin-bottom: 12px;
    }
    .content {
      flex-direction: column;
      align-items: center;
      gap: 8px;
      img {
        width: 80px;
        height: 100px;
      }
      .info li {
        font-size: 11px;
        margin-bottom: 10px;
        svg {
          font-size: 13px;
        }
      }
    }
    .description {
      font-size: 11px;
      margin-top: 10px;
      margin-bottom: 12px;
    }
    .git-container {
      padding: 5px 8px;
      p {
        font-size: 11px;
      }
      .git {
        font-size: 11px;
      }
    }
  }
}