#clone-coding {
    background-color: #f0eeeb;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.content {
    padding: 40px 20px;
    max-width: 800px;
    width: 100%;
}

.content h2 {
    @include font-gray50();
    margin-bottom: 2rem;
    text-align: center;
}

.project {
    padding: 30px;
}

.project-title {
    @include font-gray50();
    margin-bottom: 1.5rem;
    text-align: center;
}

.img-container {
    position: relative;
    width: 100%;
    overflow: hidden;
    margin-bottom: 2rem;
}

.project-img {
    width: 100%;
    height: auto;
    display: block;
}

.desc {
    @include font-gray24();
    margin-bottom: 2rem;
}
.section {
    margin-bottom: 2rem;
}
.section ul {
    list-style-type: disc;
    padding-left: 20px;
    margin: 0;
}
.section li {
    @include font-gray24();
    margin-bottom: 1rem;
}
.section li span {
    font-weight: bold;
}
.section li a {
    text-decoration: none;
    border-bottom: 1px solid #ccc;
}
.section li a:hover {
    border-bottom-color: #333;
}

// 반응형 처리
@include desktop {
    .content {
        padding: 32px 12px;
        max-width: 700px;
    }
    .project {
        padding: 20px;
    }
    .img-container {
        margin-bottom: 1.5rem;
    }
    .desc,
    .section li {
        font-size: 15px;
    }
}

@include tablet {
    #clone-coding {
        max-width: 100vw;
    }
    .content {
        padding: 18px 6px;
        max-width: 98vw;
    }
    .project {
        padding: 10px 0;
    }
    .img-container {
        margin-bottom: 1rem;
    }
    .project-title,
    .content h2 {
        font-size: 1.3rem;
    }
    .desc,
    .section li {
        font-size: 13px;
    }
}

@include mobile {
    #clone-coding {
        max-width: 100vw;
    }
    .content {
        padding: 8px 2px;
        max-width: 100vw;
    }
    .project {
        padding: 0;
    }
    .img-container {
        margin-bottom: 0.7rem;
    }
    .project-title,
    .content h2 {
        font-size: 1.1rem;
    }
    .desc,
    .section li {
        font-size: 11px;
    }
    .section ul {
        padding-left: 14px;
    }
}
